.app {
  background-color: #4a4a4a;
  position: absolute;
  height: 100%;
  width: 100%;
}
.carousel-container{
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: auto;
  transform: translate(22.5%, 22.5%)
}
.card{
  width: 150px;
  height: 140px;
  box-shadow: 3px 3px 8px lightgray;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.flex{
  display: flex;
  justify-content: space-around;
}
.product_img{
  width: 54px;
  height: 54px;
  border-radius: 6px;
  box-shadow: 3px 3px 6px lightgray;
  display: block;
  margin-top: 24px;
}
.card h5{
  margin: 0;
  font-weight: 600;
  margin-top: 12px;
}
.card p{
  margin: 0;
}